import React, { Component } from "react";
import cssClass from "./home.module.css";
import Header from "../header/header";
import Footer from "../footer/footer";
import { Link } from "react-router-dom";
import carouseImg1 from "../../assets/images/tshirt.jpg";
import carouseImg2 from "../../assets/images/tshirt.jpg";
import carouseImg3 from "../../assets/images/tshirt.jpg";
import carouseImg4 from "../../assets/images/tshirt.jpg";
import MenuItem from "../MenuItem/MenuItem";
class Home extends Component {
  state = {
    items: [],
    isLoaded: true,
  };

  // componentDidMount() {
  //   fetch("https://jsonplaceholder.typicode.com/photos")
  //     .then((res) => res.json())
  //     .then((json) => {
  //       this.setState({
  //         isLoaded: true,
  //         items: json,
  //       });
  //     });
  //   console.log(this.setState.json);
  // }
  render() {
    var { isLoaded} = this.state;
    if (!isLoaded) {
      return <div>Loading....</div>;
    } else {
      return (
        <React.Fragment className="App">
          <div className={cssClass.header}>
            <Header />
          </div>
          <div className="body">
            {/* Start Carousel  */}
            <div className="">
              <div
                id="carouselExampleCaptions"
                className={`carousel slide col-md-12 col-12 ${cssClass.Carousel}`}
                data-ride="carousel"
              >
                <ol
                  className={`carousel-indicators ${cssClass.CarouselIndicators}`}
                >
                  <li
                    data-target="#carouselExampleCaptions"
                    data-slide-to="0"
                    className="active"
                  ></li>
                  <li
                    data-target="#carouselExampleCaptions"
                    data-slide-to="1"
                  ></li>
                  <li
                    data-target="#carouselExampleCaptions"
                    data-slide-to="2"
                  ></li>
                  <li
                    data-target="#carouselExampleCaptions"
                    data-slide-to="3"
                  ></li>
                </ol>
                <div className={`carousel-inner`}>
                  <div
                    className={`carousel-item active ${cssClass.CarouselItem}`}
                    data-interval="1000"
                  >
                    <img src={carouseImg1} className="d-block w-100" alt="" />
                    <div className="carousel-caption d-none d-md-block">
                      <h3>First Slide</h3>
                    </div>
                  </div>
                  {/* <div className="d-none d-sm-block"> */}
                  <div className={`carousel-item  ${cssClass.CarouselItem}`}>
                    <img src={carouseImg2} className="d-block w-100" alt="" />
                    <div className="carousel-caption d-none d-md-block">
                      <h3>Second Slide</h3>
                    </div>
                  </div>
                  <div
                    className={`carousel-item  ${cssClass.CarouselItem}`}
                    data-interval="3000"
                  >
                    <img src={carouseImg3} className="d-block w-100" alt="" />
                    <div className="carousel-caption d-none d-md-block">
                      <h3>Third Slide</h3>
                    </div>
                  </div>
                  <div
                    className={`carousel-item  ${cssClass.CarouselItem}`}
                    data-interval="4000"
                  >
                    <img src={carouseImg4} className="d-block w-100" alt="" />
                    <div className="carousel-caption d-none d-md-block">
                      <h3>Fourth Slide</h3>
                    </div>
                  </div>
                  {/* </div> */}
                </div>

                <a
                  className="carousel-control-prev  "
                  href="#carouselExampleCaptions"
                  role="button"
                  data-slide="prev"
                >
                  <span
                    className="carousel-control-prev-icon"
                    aria-hidden="true"
                  ></span>
                  <span className="sr-only">Previous</span>
                </a>
                <a
                  className=" carousel-control-next  "
                  href="#carouselExampleCaptions"
                  role="button"
                  data-slide="next"
                >
                  <span
                    className="carousel-control-next-icon"
                    aria-hidden="true"
                  ></span>
                  <span className="sr-only">Next</span>
                </a>
              </div>
            </div>
            {/* end Carousel  */}

            <MenuItem />
          </div>
          <Footer />
        </React.Fragment>
      );
    }
  }
}
export default Home;


///////////////////////Styling//////////////////

